html {
  box-sizing: border-box;
  width: 100vw;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  color: #212121;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.App {
  grid-gap: 16px;
  grid-template-columns: 1fr;
  padding-bottom: 24px;
  display: grid;
}

.Searchbar {
  z-index: 1100;
  color: #fff;
  background-color: #3f51b5;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding: 12px 24px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px -1px #0003, 0 4px 5px #00000024, 0 1px 10px #0000001f;
}

.SearchForm {
  background-color: #fff;
  border-radius: 3px;
  align-items: center;
  width: 100%;
  max-width: 600px;
  display: flex;
  overflow: hidden;
}

.SearchForm-button {
  opacity: .6;
  cursor: pointer;
  background-image: url("https://image.flaticon.com/icons/svg/149/149852.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  border: 0;
  outline: none;
  width: 48px;
  height: 48px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.SearchForm-button:hover {
  opacity: 1;
}

.SearchForm-button-label {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.SearchForm-input {
  width: 100%;
  font: inherit;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  font-size: 20px;
  display: inline-block;
}

.SearchForm-input::placeholder {
  font: inherit;
  font-size: 18px;
}

.dots {
  justify-content: center;
  display: flex;
}

.ImageGallery {
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  max-width: calc(100vw - 48px);
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.ImageGalleryItem {
  border-radius: 2px;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.ImageGalleryItem-image {
  object-fit: cover;
  width: 100%;
  height: 260px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.ImageGalleryItem-image:hover {
  cursor: zoom-in;
  transform: scale(1.03);
}

.Overlay {
  z-index: 1200;
  background-color: #000c;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.Modal {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

.Button-wrap {
  justify-content: center;
  width: 100vw;
  display: flex;
}

.Button {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #3f51b5;
  border: 0;
  border-radius: 2px;
  min-width: 180px;
  padding: 8px 16px;
  font-family: inherit;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  box-shadow: 0 3px 1px -2px #0003, 0 2px 2px #00000024, 0 1px 5px #0000001f;
}

.Button:hover, .Button:focus {
  background-color: #303f9f;
}
